import fs from 'fs';
import path from 'path';
import { useEffect, useMemo, useState } from 'react';

const CART_STORAGE_KEY = 'sofracom.cart.v1';
const OUTPUT_CURRENCY = 'TND';
const FR_NUMBER_FORMAT = new Intl.NumberFormat('fr-TN', {
    style: 'currency',
    currency: OUTPUT_CURRENCY,
});

const ensurePath = value => {
    if (!value) return '';
    return value.startsWith('/') ? value : `/${value}`;
};

const slugify = value =>
    (value || '')
        .toLowerCase()
        .replace(/[^a-z0-9]+/g, '-')
        .replace(/^-|-$/g, '');

const normalizeProduct = (product, category) => {
    const id = `${category.slug}-${slugify(product.title)}`;
    const rawImages = Array.isArray(product.images) && product.images.length
        ? product.images
        : product.image
            ? [product.image]
            : [];
    const images = rawImages.map(ensurePath).filter(Boolean);
    const fallbackImage = images[0] || ensurePath(product.image) || '/logo.jpeg';
    const variants = Array.isArray(product.variants)
        ? product.variants.map(variant => ({
              label: variant.label || 'Variant',
              price: Number.isFinite(variant.price)
                  ? variant.price
                  : Number(variant.price) || 0,
          }))
        : [];
    const price =
        Number.isFinite(product.price) && product.price > 0
            ? product.price
            : variants[0]?.price || 0;

    return {
        ...product,
        id,
        categoryName: category.name,
        categorySlug: category.slug,
        image: fallbackImage,
        images: images.length ? images : [fallbackImage],
        datasheet: ensurePath(product.datasheet),
        variants,
        price,
        stock: product.stock || 'in',
        usage: Array.isArray(product.usage) ? product.usage : [],
    };
};

const buildCategories = rawCategories =>
    (rawCategories || []).map(category => {
        const slug = category.slug || slugify(category.name);
        return {
            ...category,
            slug,
            image: ensurePath(category.image),
            products:
                Array.isArray(category.products) && category.products.length
                    ? category.products.map(product =>
                          normalizeProduct(product, { ...category, slug })
                      )
                    : [],
        };
    });

export async function getStaticProps() {
    const dataPath = path.join(
        process.cwd(),
        'public',
        'assets',
        'data',
        'products.json'
    );
    const raw = fs.readFileSync(dataPath, 'utf-8');
    const parsed = JSON.parse(raw);
    const categories = buildCategories(parsed.categories || []);

    return {
        props: {
            categories,
        },
    };
}

const formatPrice = value => {
    if (!Number.isFinite(value)) {
        return `${value || 0} ${OUTPUT_CURRENCY}`;
    }
    return FR_NUMBER_FORMAT.format(value);
};

export default function ProductsPage({ categories = [] }) {
    const [clientCategories, setClientCategories] = useState(() => categories);
    const [activeCategorySlug, setActiveCategorySlug] = useState(null);
    const [brandFilter, setBrandFilter] = useState('');
    const [searchTerm, setSearchTerm] = useState('');
    const [selectedProductId, setSelectedProductId] = useState(null);
    const [selectedVariantIndex, setSelectedVariantIndex] = useState(0);
    const [activeImageIndex, setActiveImageIndex] = useState(0);
    const [detailStatus, setDetailStatus] = useState({ message: '', type: '' });
    const [cart, setCart] = useState([]);
    const [cartOpen, setCartOpen] = useState(false);
    const [checkoutStatus, setCheckoutStatus] = useState({ message: '', type: '' });
    const [checkoutSubmitting, setCheckoutSubmitting] =
        useState(false);
    const [checkoutForm, setCheckoutForm] = useState({
        name: '',
        phone: '',
        address: '',
        notes: '',
    });

    useEffect(() => {
        if (categories.length) {
            setClientCategories(categories);
        }
    }, [categories]);

    useEffect(() => {
        if (clientCategories.length) return;
        let canceled = false;
        (async () => {
            try {
                const response = await fetch('/assets/data/products.json');
                if (!response.ok) {
                    console.warn('[products] client fetch failed', response.status);
                    return;
                }
                const data = await response.json();
                if (canceled) return;
                setClientCategories(buildCategories(data.categories || []));
            } catch (error) {
                console.error('[products] failed to load categories', error);
            }
        })();
        return () => {
            canceled = true;
        };
    }, [clientCategories.length]);

    const categoriesToRender = clientCategories.length
        ? clientCategories
        : categories;

    const productMap = useMemo(() => {
        const map = new Map();
        categoriesToRender.forEach(category => {
            (category.products || []).forEach(product => {
                map.set(product.id, product);
            });
        });
        return map;
    }, [categoriesToRender]);

    const activeCategory = categoriesToRender.find(
        category => category.slug === activeCategorySlug
    );

    const availableBrands = useMemo(() => {
        if (!activeCategory) return [];
        const brands = Array.from(
            new Set(activeCategory.products.map(product => product.brand).filter(Boolean))
        );
        return brands.sort();
    }, [activeCategory]);

    useEffect(() => {
        if (!availableBrands.includes(brandFilter)) {
            setBrandFilter('');
        }
    }, [availableBrands, brandFilter]);

    useEffect(() => {
        const stored = typeof window !== 'undefined'
            ? window.localStorage.getItem(CART_STORAGE_KEY)
            : null;
        if (stored) {
            try {
                const parsed = JSON.parse(stored);
                if (Array.isArray(parsed)) {
                    setCart(parsed);
                }
            } catch (error) {
                console.warn('[cart] failed to read storage', error);
            }
        }
    }, []);

    useEffect(() => {
        if (typeof window === 'undefined') return;
        try {
            window.localStorage.setItem(CART_STORAGE_KEY, JSON.stringify(cart));
        } catch (error) {
            console.warn('[cart] failed to persist', error);
        }
    }, [cart]);

    const filteredProducts = useMemo(() => {
        if (!activeCategory) return [];
        const normalizedTerm = searchTerm.trim().toLowerCase();
        return activeCategory.products.filter(product => {
            if (brandFilter && product.brand !== brandFilter) return false;
            if (!normalizedTerm) return true;
            const haystack = `${product.title} ${product.description} ${product.brand}`.toLowerCase();
            return haystack.includes(normalizedTerm);
        });
    }, [activeCategory, brandFilter, searchTerm]);

    const selectedProduct = selectedProductId
        ? productMap.get(selectedProductId)
        : null;

    useEffect(() => {
        setActiveImageIndex(0);
        setSelectedVariantIndex(0);
        setDetailStatus({ message: '', type: '' });
    }, [selectedProductId]);

    const cartCount = useMemo(
        () => cart.reduce((total, item) => total + (item.quantity || 0), 0),
        [cart]
    );

    const cartTotal = useMemo(
        () =>
            cart.reduce((total, item) => {
                if (Number.isFinite(item.price)) {
                    return total + item.price * item.quantity;
                }
                return total;
            }, 0),
        [cart]
    );

    const handleSearchChange = event => {
        setSearchTerm(event.target.value);
    };

    const openCart = () => setCartOpen(true);
    const closeCart = () => setCartOpen(false);

    const updateCartQuantity = (id, quantity) => {
        setCart(existing =>
            existing
                .map(item =>
                    item.id === id
                        ? { ...item, quantity: Math.max(1, quantity) }
                        : item
                )
                .filter(item => item.quantity > 0)
        );
    };

    const removeCartItem = id => {
        setCart(existing => existing.filter(item => item.id !== id));
    };

    const addToCart = (product, variant) => {
        if (!product) return;
        const price = variant?.price ?? product.price ?? 0;
        const variantLabel = variant?.label;
        const cartId = variantLabel ? `${product.id}-${slugify(variantLabel)}` : product.id;
        setCart(existing => {
            const existingItem = existing.find(item => item.id === cartId);
            if (existingItem) {
                return existing.map(item =>
                    item.id === cartId
                        ? { ...item, quantity: item.quantity + 1 }
                        : item
                );
            }
            return [
                ...existing,
                {
                    id: cartId,
                    title: product.title,
                    price,
                    quantity: 1,
                    image: product.images[0] || product.image,
                    category: product.categoryName,
                    brand: product.brand,
                    variantLabel,
                },
            ];
        });
        setDetailStatus({
            message: 'Added to cart',
            type: 'success',
        });
        openCart();
    };

    const handleCheckoutInput = event => {
        const { name, value } = event.target;
        setCheckoutForm(form => ({ ...form, [name]: value }));
    };

    const handleCheckoutSubmit = async event => {
        event.preventDefault();
        if (!cart.length) {
            setCheckoutStatus({
                message: 'Cart is empty',
                type: 'error',
            });
            return;
        }
        if (!checkoutForm.name || !checkoutForm.phone || !checkoutForm.address) {
            setCheckoutStatus({
                message: 'Name, phone, and address are required.',
                type: 'error',
            });
            return;
        }
        setCheckoutSubmitting(true);
        setCheckoutStatus({ message: 'Sending order…', type: '' });
        const payload = {
            customer: checkoutForm,
            items: cart.map(item => ({
                id: item.id,
                title: item.title,
                price: item.price,
                quantity: item.quantity,
                image: item.image,
                category: item.category,
                brand: item.brand,
                variantLabel: item.variantLabel,
            })),
            total: cartTotal,
            currency: OUTPUT_CURRENCY,
        };
        try {
            const response = await fetch('/api/create-order', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(payload),
            });
            const result = await response.json();
            if (!response.ok || !result.ok) {
                throw new Error(result.error || 'Unable to place order');
            }
            setCart([]);
            setCheckoutForm({
                name: '',
                phone: '',
                address: '',
                notes: '',
            });
            setCheckoutStatus({
                message: 'Order submitted! We will confirm shortly.',
                type: 'success',
            });
        } catch (error) {
            console.error('[order] submit failed', error);
            setCheckoutStatus({
                message: error.message || 'Unable to submit order.',
                type: 'error',
            });
        } finally {
            setCheckoutSubmitting(false);
        }
    };

    const detailVariant =
        selectedProduct?.variants?.[selectedVariantIndex] ?? null;
    const detailPrice =
        detailVariant?.price ?? selectedProduct?.price ?? 0;
    const detailImage =
        selectedProduct?.images?.[activeImageIndex] || selectedProduct?.image;

    const renderedProducts = (
        filteredProducts.length ? filteredProducts : activeCategory?.products || []
    );

    return (
        <>
            {/* Breadcrumb & Controls */}
            <main className="max-w-7xl mx-auto px-6 py-12">
                <div className="flex flex-wrap items-center justify-between gap-3 mb-6">
                    <nav className="text-sm text-gray-500" id="breadcrumb">
                        <a href="/" className="hover:underline">
                            Home
                        </a>
                        <span className="mx-1">/</span>
                        <span className="text-gray-700 font-medium">Products</span>
                    </nav>
                    <div className="flex items-center gap-3">
                        {activeCategory && (
                            <>
                                <select
                                    id="brandFilter"
                                    className="px-3 py-2 rounded-lg border focus:ring-2 focus:ring-blue-200"
                                    value={brandFilter}
                                    onChange={event =>
                                        setBrandFilter(event.target.value)
                                    }
                                >
                                    <option value="">All brands</option>
                                    {availableBrands.map(brand => (
                                        <option key={brand} value={brand}>
                                            {brand}
                                        </option>
                                    ))}
                                </select>
                                <input
                                    id="searchBox"
                                    type="search"
                                    value={searchTerm}
                                    onChange={handleSearchChange}
                                    placeholder="Search products..."
                                    className="px-3 py-2 rounded-lg border focus:ring-2 focus:ring-blue-200"
                                />
                                <button
                                    id="backBtn"
                                    type="button"
                                    className="px-3 py-2 rounded-lg border hover:bg-gray-100"
                                    onClick={() => {
                                        setActiveCategorySlug(null);
                                        setBrandFilter('');
                                        setSearchTerm('');
                                        setSelectedProductId(null);
                                    }}
                                >
                                    ← Back to categories
                                </button>
                            </>
                        )}
                    </div>
                </div>

                {/* Categories */}
                {!activeCategory && (
                    <section id="categoriesSection">
                        <h2 className="text-2xl font-extrabold mb-4">
                            Categories
                        </h2>
                        <div className="grid sm:grid-cols-2 lg:grid-cols-3 gap-6">
                            {categoriesToRender.map(category => (
                                <article
                                    key={category.slug}
                                    className="product-card hover:cursor-pointer"
                                    data-animate
                                    onClick={() => setActiveCategorySlug(category.slug)}
                                    data-tilt
                                >
                                    <img
                                        src={category.image}
                                        alt={category.name}
                                        className="card-img"
                                    />
                                    <div className="p-6">
                                        <header className="text-lg font-semibold text-gray-900">
                                            {category.name}
                                        </header>
                                        <p className="mt-3 text-sm text-gray-600 leading-relaxed">
                                            {category.description}
                                        </p>
                                        <div className="mt-4 text-xs uppercase tracking-wide text-blue-700 font-semibold">
                                            {category.products.length} products
                                        </div>
                                    </div>
                                </article>
                            ))}
                        </div>
                    </section>
                )}

                {/* Products */}
                {activeCategory && (
                    <section id="productsSection">
                        <div className="pt-6">
                            <h2
                                id="catTitle"
                                className="text-2xl font-extrabold text-gray-900"
                            >
                                {activeCategory.name}
                            </h2>
                            <p
                                id="catDesc"
                                className="text-sm text-gray-500 mt-1"
                            >
                                {activeCategory.description}
                            </p>
                        </div>
                        <div className="mt-6 grid sm:grid-cols-2 lg:grid-cols-3 gap-6">
                            {renderedProducts.map(product => (
                                <article
                                    key={product.id}
                                    className={`product-card ${
                                        product.stock === 'out' ? 'product-out' : ''
                                    }`}
                                    data-animate
                                    data-tilt
                                >
                                    <img
                                        src={product.image}
                                        alt={product.title}
                                        className="card-img"
                                    />
                                    <div className="p-6 flex flex-col gap-3">
                                        <div className="flex items-center justify-between">
                                            <h3 className="text-lg font-semibold text-gray-900">
                                                {product.title}
                                            </h3>
                                            <span className="text-xs text-gray-500 uppercase">
                                                {product.brand}
                                            </span>
                                        </div>
                                        <p className="product-description">
                                            {product.description}
                                        </p>
                                        <div className="product-price-row">
                                            <span
                                                className="product-price"
                                                data-product-price
                                            >
                                                {formatPrice(product.price)}
                                            </span>
                                            <span
                                                className={`stock-badge ${
                                                    product.stock === 'in'
                                                        ? 'stock-badge--in'
                                                        : 'stock-badge--out'
                                                }`}
                                            >
                                                {product.stock === 'in'
                                                    ? 'In stock'
                                                    : 'Out of stock'}
                                            </span>
                                        </div>
                                        <div className="flex gap-2 flex-wrap">
                                            {product.usage.map(tag => (
                                                <span
                                                    className="tag px-3 py-1 rounded-full text-xs"
                                                    key={`${product.id}-${tag}`}
                                                >
                                                    {tag}
                                                </span>
                                            ))}
                                        </div>
                                        <div className="product-actions mt-auto">
                                            <button
                                                type="button"
                                                className="product-detail-btn"
                                                onClick={() => {
                                                    setSelectedProductId(product.id);
                                                    setSelectedVariantIndex(0);
                                                    setActiveImageIndex(0);
                                                }}
                                            >
                                                View details
                                            </button>
                                            <button
                                                type="button"
                                                className="cart-submit px-4 py-2 text-sm"
                                                onClick={() =>
                                                    addToCart(product, product.variants[0])
                                                }
                                            >
                                                Add to cart
                                            </button>
                                        </div>
                                    </div>
                                </article>
                            ))}
                        </div>
                    </section>
                )}

                {/* Detail Panel */}
                {selectedProduct && (
                    <section
                        id="productDetailSection"
                        className="mt-10 detail-panel rounded-3xl border border-gray-200 bg-white p-6 shadow-2xl"
                    >
                        <div className="detail-header flex items-center justify-between gap-4">
                            <div
                                id="detailBreadcrumb"
                                className="detail-breadcrumb"
                            >
                                <button
                                    type="button"
                                    className="detail-crumb"
                                    onClick={() => setActiveCategorySlug(null)}
                                >
                                    Categories
                                </button>
                                <span>•</span>
                                <span>{selectedProduct.categoryName}</span>
                            </div>
                            <button
                                type="button"
                                id="productDetailClose"
                                className="detail-close text-sm text-gray-500 border border-gray-200 rounded-full px-3 py-1 hover:border-gray-400 hover:text-gray-900 transition"
                                onClick={() => setSelectedProductId(null)}
                            >
                                Close
                            </button>
                        </div>
                        <div className="detail-layout grid gap-6 lg:grid-cols-2 mt-6">
                            <div className="detail-gallery">
                                <img
                                    id="detailMainImage"
                                    src={detailImage}
                                    alt={selectedProduct.title}
                                    className="detail-main-img block w-full rounded-2xl border border-gray-200 bg-gray-50 object-contain"
                                />
                                {selectedProduct.images.length > 1 && (
                                    <div
                                        id="detailThumbs"
                                        className="detail-thumbs mt-4 flex flex-wrap gap-3"
                                    >
                                        {selectedProduct.images.map((src, index) => (
                                            <img
                                                key={src}
                                                src={src}
                                                alt={`${selectedProduct.title} ${index}`}
                                                className={`rounded-2xl cursor-pointer ${
                                                    activeImageIndex === index
                                                        ? 'active'
                                                        : ''
                                                }`}
                                                onClick={() =>
                                                    setActiveImageIndex(index)
                                                }
                                            />
                                        ))}
                                    </div>
                                )}
                            </div>
                            <div className="detail-meta flex flex-col gap-4">
                                <div>
                                    <p className="text-xs text-gray-500" id="detailCategoryName">
                                        {selectedProduct.categoryName}
                                    </p>
                                    <h2
                                        id="detailTitle"
                                        className="text-3xl font-semibold text-gray-900 leading-tight"
                                    >
                                        {selectedProduct.title}
                                    </h2>
                                    <p
                                        id="detailBrand"
                                        className="text-sm text-gray-500 mt-1"
                                    >
                                        {selectedProduct.brand}
                                    </p>
                                </div>
                                <div
                                    id="detailUsage"
                                    className="flex flex-wrap gap-2"
                                >
                                    {selectedProduct.usage.map(tag => (
                                        <span
                                            key={`${selectedProduct.id}-usage-${tag}`}
                                            className="tag px-3 py-1 rounded-full text-xs"
                                        >
                                            {tag}
                                        </span>
                                    ))}
                                </div>
                                <div className="detail-price-row flex flex-wrap items-center gap-3">
                                    <span
                                        id="detailPrice"
                                        className="text-3xl font-bold text-gray-900"
                                    >
                                        {formatPrice(detailPrice)}
                                    </span>
                                    <span
                                        id="detailStockBadge"
                                        className={`stock-badge ${
                                            selectedProduct.stock === 'in'
                                                ? 'stock-badge--in'
                                                : 'stock-badge--out'
                                        }`}
                                    >
                                        {selectedProduct.stock === 'in'
                                            ? 'In stock'
                                            : 'Out of stock'}
                                    </span>
                                </div>
                                <div
                                    id="detailVariants"
                                    className="detail-variants flex flex-wrap gap-2"
                                >
                                    {selectedProduct.variants.length ? (
                                        selectedProduct.variants.map(
                                            (variant, index) => (
                                                <button
                                                    key={`${variant.label}-${index}`}
                                                    type="button"
                                                    className={`detail-variant-btn ${
                                                        selectedVariantIndex === index
                                                            ? 'active'
                                                            : ''
                                                    }`}
                                                    onClick={() =>
                                                        setSelectedVariantIndex(
                                                            index
                                                        )
                                                    }
                                                >
                                                    {variant.label}
                                                    <span>
                                                        {formatPrice(variant.price)}
                                                    </span>
                                                </button>
                                            )
                                        )
                                    ) : (
                                        <span className="text-sm text-gray-500">
                                            Single configuration
                                        </span>
                                    )}
                                </div>
                                <p
                                    id="detailDescription"
                                    className="text-sm text-gray-700 leading-relaxed"
                                >
                                    {selectedProduct.description}
                                </p>
                                {selectedProduct.datasheet && (
                                    <a
                                        id="detailDatasheet"
                                        href={selectedProduct.datasheet}
                                        target="_blank"
                                        rel="noreferrer"
                                        className="datasheet-link"
                                    >
                                        <svg
                                            viewBox="0 0 24 24"
                                            fill="none"
                                            stroke="currentColor"
                                            strokeWidth="2"
                                        >
                                            <path d="M4 17v-6a4 4 0 014-4h8a4 4 0 014 4v6" />
                                            <path d="M10 14l3 3 6-6" />
                                        </svg>
                                        Download datasheet
                                    </a>
                                )}
                                <button
                                    id="detailAddBtn"
                                    type="button"
                                    className="w-full rounded-2xl bg-blue-600 px-4 py-3 text-sm font-semibold text-white shadow hover:bg-blue-700 transition"
                                    onClick={() =>
                                        addToCart(
                                            selectedProduct,
                                            detailVariant
                                        )
                                    }
                                    disabled={selectedProduct.stock !== 'in'}
                                >
                                    Add to cart
                                </button>
                                <p
                                    id="detailStatus"
                                    className={`quote-status ${detailStatus.type} mt-4 text-sm ${
                                        detailStatus.message ? '' : 'hidden'
                                    }`}
                                    aria-live="polite"
                                >
                                    {detailStatus.message}
                                </p>
                            </div>
                        </div>
                    </section>
                )}
            </main>

            {/* Cart FAB */}
            <button
                id="cartFab"
                className="cart-fab"
                type="button"
                onClick={() => {
                    openCart();
                    setCheckoutStatus({ message: '', type: '' });
                }}
            >
                <span>Cart</span>
                <span className="px-2 py-1 rounded-full bg-white/15 text-sm font-semibold">
                    {cartCount}
                </span>
            </button>

            <div
                id="cartOverlay"
                className={`cart-overlay ${cartOpen ? 'active' : ''}`}
                onClick={closeCart}
            />

            <aside
                id="cartDrawer"
                className={`cart-drawer ${cartOpen ? 'open' : ''}`}
                aria-hidden={!cartOpen}
            >
                <div className="cart-header">
                    <div>
                        <h3
                            id="cartTitle"
                            className="text-lg font-semibold text-gray-900"
                        >
                            Your order
                        </h3>
                        <p className="text-sm text-gray-500">
                            Review items then confirm your details.
                        </p>
                    </div>
                    <button
                        id="cartClose"
                        type="button"
                        aria-label="Close cart"
                        className="text-2xl leading-none text-gray-500 hover:text-gray-800"
                        onClick={closeCart}
                    >
                        ×
                    </button>
                </div>
                <div className="cart-body" id="cartItems">
                    {cart.length ? (
                        cart.map(item => (
                            <div
                                className="cart-item"
                                key={item.id}
                                data-id={item.id}
                            >
                                <img src={item.image} alt={item.title} />
                                <div>
                                    <p className="cart-item-title">
                                        {item.title}
                                    </p>
                                    <p className="text-xs text-gray-500">
                                        {item.variantLabel
                                            ? item.variantLabel
                                            : item.category}
                                    </p>
                                    <div className="cart-qty mt-2">
                                        <button
                                            type="button"
                                            data-action="decrement"
                                            onClick={() =>
                                                updateCartQuantity(
                                                    item.id,
                                                    item.quantity - 1
                                                )
                                            }
                                        >
                                            −
                                        </button>
                                        <input
                                            type="number"
                                            min="1"
                                            value={item.quantity}
                                            onChange={event => {
                                                const value = Number(
                                                    event.target.value
                                                );
                                                if (!Number.isFinite(value)) return;
                                                updateCartQuantity(item.id, value);
                                            }}
                                        />
                                        <button
                                            type="button"
                                            data-action="increment"
                                            onClick={() =>
                                                updateCartQuantity(
                                                    item.id,
                                                    item.quantity + 1
                                                )
                                            }
                                        >
                                            +
                                        </button>
                                        <button
                                            type="button"
                                            data-action="remove"
                                            onClick={() =>
                                                removeCartItem(item.id)
                                            }
                                            className="text-xs text-red-500 ml-3"
                                        >
                                            Remove
                                        </button>
                                    </div>
                                </div>
                            </div>
                        ))
                    ) : (
                        <p className="empty-cart">Your cart is empty.</p>
                    )}
                </div>
                <div className="cart-summary">
                    <div className="cart-summary-row">
                        <span>Total</span>
                        <span id="cartTotal">{formatPrice(cartTotal)}</span>
                    </div>
                    <p
                        id="cartAlert"
                        className={`cart-alert ${checkoutStatus.type}`}
                        role="status"
                    >
                        {checkoutStatus.message}
                    </p>
                </div>
                <form
                    id="checkoutForm"
                    className="cart-form"
                    onSubmit={handleCheckoutSubmit}
                    noValidate
                >
                    <label>
                        <span>Full name</span>
                        <input
                            type="text"
                            name="name"
                            value={checkoutForm.name}
                            onChange={handleCheckoutInput}
                            required
                        />
                    </label>
                    <label>
                        <span>Phone</span>
                        <input
                            type="tel"
                            name="phone"
                            value={checkoutForm.phone}
                            onChange={handleCheckoutInput}
                            required
                        />
                    </label>
                    <label>
                        <span>Delivery address</span>
                        <textarea
                            name="address"
                            rows="2"
                            value={checkoutForm.address}
                            onChange={handleCheckoutInput}
                            required
                        />
                    </label>
                    <label>
                        <span>Notes (optional)</span>
                        <textarea
                            name="notes"
                            rows="2"
                            value={checkoutForm.notes}
                            onChange={handleCheckoutInput}
                        />
                    </label>
                    <button
                        id="cartSubmitBtn"
                        type="submit"
                        className="cart-submit"
                        disabled={checkoutSubmitting}
                    >
                        {checkoutSubmitting ? 'Sending…' : 'Send order'}
                    </button>
                </form>
            </aside>
        </>
    );
}
